<template>
  <v-container fluid fill-height style="padding:0;" id="fullscreen" grid-list-md>
    <Blog :items="items" :time="time" :title="title">
      <div class="blogcontent">
        <v-layout row wrap>
          <v-flex xs12>
            <v-hover>
              <v-card
                slot-scope="{ hover }"
                :class="`elevation-${hover ? 12 : 2}`"
                min-height="200"
              >
                <v-card-title>
                  <h3 id="full1">全屏实现方式一</h3>
                </v-card-title>
                <v-divider></v-divider>
                <v-card-text>
                    <v-flex xs12>
                        <div id="box" class="box">点击我</div>
                    </v-flex>

                    <v-flex xs12>
                        <v-tabs color="light-green darken-1" dark slider-color="yellow">
                            <v-tab key="js" ripple>JAVASCRIPT</v-tab>
                            <v-tab key="html" ripple>HTML</v-tab>
                            <v-tab key="css" ripple>CSS</v-tab>

                            <v-tab-item key="js">
                                <v-card flat>
                                    <v-card-text>
                                        <pre class="code">{{pres[1]}}</pre>
                                    </v-card-text>
                                </v-card>
                            </v-tab-item>

                            <v-tab-item key="html">
                                <v-card flat>
                                    <v-card-text>
                                        <pre class="code">{{pres[0]}}</pre>
                                    </v-card-text>
                                </v-card>
                            </v-tab-item>

                            <v-tab-item key="css">
                                <v-card flat>
                                    <v-card-text>
                                        <pre class="code">{{pres[2]}}</pre>
                                    </v-card-text>
                                </v-card>
                            </v-tab-item>
                        </v-tabs>
                    </v-flex>
                </v-card-text>
              </v-card>
            </v-hover>
          </v-flex>

          <v-flex xs12>
            <v-hover>
              <v-card
                slot-scope="{ hover }"
                :class="`elevation-${hover ? 12 : 2}`"
                min-height="200"
              >
                <v-card-title>
                  <h3 id="full2">全屏实现方式二</h3>
                </v-card-title>
                <v-divider></v-divider>
                <v-card-text>
                    <v-flex xs12>
                        <div class="box" id="box1">点击我</div>
                    </v-flex>

                    <v-flex xs12>
                        <v-tabs color="light-green darken-1" dark slider-color="yellow">
                            <v-tab key="js" ripple>JAVASCRIPT</v-tab>
                            <v-tab key="html" ripple>HTML</v-tab>
                            <v-tab key="css" ripple>CSS</v-tab>

                            <v-tab-item key="js">
                                <v-card flat>
                                    <v-card-text>
                                        <pre class="code">{{pres[3]}}</pre>
                                    </v-card-text>
                                </v-card>
                            </v-tab-item>

                            <v-tab-item key="html">
                                <v-card flat>
                                    <v-card-text>
                                        <pre class="code">{{pres[0]}}</pre>
                                    </v-card-text>
                                </v-card>
                            </v-tab-item>

                            <v-tab-item key="css">
                                <v-card flat>
                                    <v-card-text>
                                        <pre class="code">{{pres[2]}}</pre>
                                    </v-card-text>
                                </v-card>
                            </v-tab-item>
                        </v-tabs>
                    </v-flex>
                </v-card-text>
              </v-card>
            </v-hover>
          </v-flex>
        </v-layout>
      </div>
    </Blog>
  </v-container>
</template>

<script>
import Blog from "../Blog.vue";
export default {
  components: { Blog },
  data() {
    return {
      rect: null,
      time: "2019-4-10",
      title: "Dom元素全屏",
      items: [
        {
          title: "全屏实现方式一",
          target: "#full1"
        },
        { divider: true },
        {
          title: "全屏实现方式二",
          target: "#full2"
        },
      ],
      pres: 
[
`   
    <div id="box">点击我</div>
`,
`
    const box = document.querySelector("#box");
    if (box.requestFullscreen) { // 是否支持全屏
        let isFullScreen = false; 
        box.onfullscreenchange = function ( event ) { // 触发全屏就会调用此方法
            console.log('1');
            isFullScreen = !isFullScreen;
        };

        box.onfullscreenerror = function ( event ) { 
            console.log("全屏api出错");
        };

        box.addEventListener('click', () => {
            console.log('2');
            if(!isFullScreen){
                box.requestFullscreen().then(() => {}).catch(err => { // 全屏
                    console.log(err);
                })
            }else{
                document.exitFullscreen(); // 退出全屏，只能通过document调用
            }
        })
    }
`,
`
    #box {
        background-color: antiquewhite;
        width: 200px;
        height: 200px;
        font-size: 15px;
        color:coral;
        display: flex;
        justify-content: center;
        align-items: center;
    }
`,
`
const box = document.querySelector("#box");
if (box.requestFullscreen) {
    box.addEventListener('click', () => {
        if (!document.fullscreenElement) { // 判断当前document中是否有全屏元素，如果没有则全屏，有则退出全屏
            box.requestFullscreen();
        } else {
            if (document.exitFullscreen) {
                document.exitFullscreen(); 
            }
        }
    })
}
`
]
    };
  },
    mounted() {
        const box = document.querySelector("#box");
        if (box.requestFullscreen) {
            let isFullScreen = false; 
            box.onfullscreenchange = function ( event ) { 
                console.log('1');
                isFullScreen = !isFullScreen;
            };

            box.onfullscreenerror = function ( event ) { 
                console.log("全屏api出错");
            };

            box.addEventListener('click', () => {
                console.log('2');
                if(!isFullScreen){
                    box.requestFullscreen().then(() => {}).catch(err => {
                        console.log(err);
                    })
                }else{
                    document.exitFullscreen();
                }
            })
        }

        const box1 = document.querySelector("#box1");
        if (box1.requestFullscreen) {
            box1.addEventListener('click', () => {
                if (!document.fullscreenElement) {
                    box1.requestFullscreen();
                } else {
                    if (document.exitFullscreen) {
                        document.exitFullscreen(); 
                    }
                }
            })
        }
    },
    methods: {
    },
    destroyed() {

    },
};
</script>

<style lang="scss">
#fullscreen {
    .blogcontent {
        padding-top: 20px;
    }
    .code {
        font-size: 12px;
        background-color: #f9f9f5;
        white-space: pre-wrap;
        word-wrap: break-word;
    }

    .box {
        background-color: antiquewhite;
        width: 200px;
        height: 200px;
        font-size: 15px;
        color:coral;
        display: flex;
        justify-content: center;
        align-items: center;
    }
}
</style>

